import React from 'react';
import { connect } from 'react-redux';
import AboutUs from './AboutUs';
import { getAboutUsThunk } from '../../redux/contact-reducer';
import { useEffect } from 'react';


const AboutUsContainer = (props) => {
    useEffect( () => {
        props.getAboutUsThunk();
    }, [])
    return <AboutUs lang = {props.lang} aboutUs = { props.aboutUs }/>
}


let mapStateToProps = (state) => {
    return {
        lang: state.appPage.lang,
        aboutUs: state.contactPage.aboutUs,
    }
}
export default connect(mapStateToProps, {
    getAboutUsThunk
})(AboutUsContainer);