import { orderAPI } from "../api/api";

const SET_SUCCESS_HELPER = 'SET_SUCCESS_HELPER';


let initailState = {
    isHelper: false,
}

const orderReducer = (state = initailState, action) => {
    switch (action.type) {
        case SET_SUCCESS_HELPER:
            return {
                ...state,
                isHelper: action.isHelper
            }
        default:
            return state;
    }
}

const setSuccessHelper = (isHelper) => ({ type: SET_SUCCESS_HELPER, isHelper });

export const postOrderDataThunk = (email, name, phone, adress, comment, products) => (dispatch) => {
    orderAPI.postOrderData(email, name, phone, adress, comment, products)
        .then(response => {
            console.log(response)
        })
}

export default orderReducer;