import { detailAPI } from "../api/api";

const SET_PRODUCT_DETAIL = 'SET_PRODUCT_DETAIL';
const SET_EVENT_DETAIL = 'SET_EVENT_DETAIL';
const SET_EVENT_PRODUCT = 'SET_EVENT_PRODUCT';





let initailState = {
    detail: null,
    eventDetail: null,
    eventProduct: [],
    

}

const detailReducer = (state = initailState, action) => {
    switch (action.type) {
        case SET_PRODUCT_DETAIL:
            return {
                ...state,
                detail: action.detail
            }
        case SET_EVENT_DETAIL:
            return {
                ...state,
                eventDetail: action.eventDetail
            }
        case SET_EVENT_PRODUCT:
            return {
                ...state,
                eventProduct: action.eventProduct
            }
        default:
            return state;
    }
}

const setProductDetail = (detail) => ( {type: SET_PRODUCT_DETAIL, detail} );
const setEventDetail = (eventDetail) =>  ( {type: SET_EVENT_DETAIL, eventDetail} );
const setEventProduct = (eventProduct) => ( {type: SET_EVENT_PRODUCT, eventProduct} );


export const getProductDetailThunk = (detailID) => {
    return (dispatch) => {
        detailAPI.getDetailProduct(detailID).then(data => {
            dispatch(setProductDetail(data));
        });
    }
}
export const getEventDetailThunk = (eventID) => (dispatch) => {
        detailAPI.getDetailEvent(eventID).then(data => {
            dispatch(setEventDetail(data));
        })    
}

export const getEventProductThunk = (eventID) => (dispatch) => {
    detailAPI.getProductEvent(eventID).then(data => {
       
        dispatch(setEventProduct(data));
    });
}


export default detailReducer;

