import { filterAPI } from "../api/api";

const { act } = require("react-dom/test-utils");

const SET_BRAND_FILTER = 'SET_BRAND_FILTER';
const SET_MATERIAL_FILTER = 'SET_MATERIAL_FILTER';

const SET_OPT_POWER_FILTER = 'SET_OPT_POWER_FILTER';
const SET_RADIUS_FILTER = 'SET_RADIUS_FILTER';
const SET_VOLUME_FILTER = 'SET_VOLUME_FILTER';



let initalState = {
    brands: [],
    materials: [],
    sex: [ {id: 1, name: "man"}, {id: 2, name: "woman"}, {id: 3, name: "unisex"}, {id: 4, name: "child"} ],
    radius: [],
    volumes: [],
    powers: [],


}

const filterReducer = (state = initalState, action) => {
    switch (action.type) {
        case SET_BRAND_FILTER:
            return {
                ...state,
                brands: action.brands
            }
        case SET_MATERIAL_FILTER:
            return {
                ...state,
                materials: action.materials
            }
        case SET_OPT_POWER_FILTER:
            return {
                ...state,
                powers: action.powers
            }
        case SET_RADIUS_FILTER:
            return {
                ...state,
                radius: action.radius
            }
        case SET_VOLUME_FILTER:
            return {
                ...state,
                volumes: action.volumes
            }

        default:
            return state
    } 
}

const setBrands = (brands) => ( {type: SET_BRAND_FILTER, brands} );
const setMaterials = (materials) => ( {type: SET_MATERIAL_FILTER, materials} );
const setPowers = (powers) => ( {type: SET_OPT_POWER_FILTER, powers} );
const setRadius = (radius) => ( {type: SET_RADIUS_FILTER, radius} );
const setVolumes = (volumes) => ( {type: SET_VOLUME_FILTER, volumes} );




export const getBrandsThunk = (cat) => (dispatch) => {
    filterAPI.getBrands(cat).then(data => {
        dispatch(setBrands(data));
    });
}

export const getMaterailsThunk = (cat) => (dispatch) => {
    filterAPI.getMaterials(cat).then(data => {
        dispatch(setMaterials(data));
    });
}

export const getRadiusThunk = (cat) => (dispatch) => {
    filterAPI.getRadius(cat).then(data => {
        dispatch(setRadius(data));
    });
}

export const getVolumesThunk = (cat) => (dispatch) => {
    filterAPI.getVolumes(cat).then(data => {
        dispatch(setVolumes(data));
    });
}

export const getPowersThunk = (cat) => (dispatch) => {
    filterAPI.getPowers(cat).then(data => {
        dispatch(setPowers(data));
    });
}

export default filterReducer;


