import React from 'react';
import { NavLink } from 'react-router-dom';
import './ItemProductModule.css';
import { useCookies } from 'react-cookie';
import { useRef } from 'react';
import { useState } from 'react';
import {toast} from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

toast.configure();
const ItemProduct = (props) => {

	const [ cookie, setCookie ] = useCookies(['product']);


	let products = [];

	
	const combineById = (object) => {
		let result = object.map( u => u.id);
		return result.toString();
		
	}

	// ALERT 

	

	

	const addProduct = (event) => {
		toast.error("   ");
		let isTrue = true
		if ( cookie.product != null ) {
			cookie.product.map( (u, ide) => {
				if (u.id === event) {
					products = cookie.product;
					products[ide].count += 1;

					let temp = combineById(products);

					props.getCartsThunk(temp);

					setCookie('product', products, {path: '/'});
					isTrue = false;	

					
				}
			});

			if (isTrue) {
				let state = {id: event, count: 1};
				products = cookie.product;
				products.push(state);
				products.sort( (a, b) => {
					return a.id - b.id;
				});

				let temp = combineById(products);
				props.getCartsThunk(temp);
				setCookie('product', products, {path: '/'});
				
			}
			
			
		}
		else {
			let state = {id: event, count: 1};
			products.push(state);
			products.sort( (a, b) => {
				return a.id - b.id;
			});
			let temp = combineById(products);
			props.getCartsThunk(temp);
			setCookie('product', products, {path: '/'});	

			
			
		}
	}

	
	
    return (
        <div class="col-xl-3 col-lg-4 col-md-6 mb-3">
					<div class="app-product">
						
						{props.is_discount &&  props.discount != 0 && <div class="app-product-discount">
							<span class="app-product-flag-discount"> {props.discount} %  </span>
						</div>}
						
						<NavLink class="thumb" to = {`/product/${props.id}`}>
							<img src = {props.img_1} width="100%" alt="" />
						</NavLink>

						<div class="products-detail">
							<NavLink class="product_title" to = {`/product/${props.id}`}> {props.title} </NavLink>
							<div class="grid-base d-flex justify-content-between container">
								<div class="d-flex">
									<div class="grid-link_org_price mr-2">
										{props.is_discount && props.discount != 0 ? <span> {props.price * (1 - (props.discount / 100 ))} KZT </span>: <span> {props.price} KZT </span>}
									</div>
								</div>


								<button class="btn icon-cart" onClick = { () =>  addProduct(props.id) } value = {"hello"} > 
									<i class="fa fa-shopping-cart" aria-hidden="true" ></i>
								</button>
							</div>
							<del class="grid-link_sale_price py-1 pl-3">
								{props.is_discount && props.discount != 0 &&  <span> {props.price} kzt </span>}
							</del>
						</div>
					</div>
				</div>
    )
}

export default ItemProduct;