import React from 'react';
import './HeaderMidModule.css';
import logo from '../../../assets/images/1547.svg';
import { NavLink } from 'react-router-dom';
import { useCookies } from 'react-cookie';



const HeaderMid = (props) => {
    const [ cookie, setCookie ] = useCookies(['product']);
    
    return (
        <nav className="navbar border-bottom app-mid-header">
               
            <a href = {'/'} id='app-base-logo'> <img src = {logo} alt="" width="100%" /></a>
            <div className="d-flex justify-content-between app-mid-header-logos">
                <a href={props.contacts.insta_url}><i className="fa fa-instagram" aria-hidden="true"></i></a>
                <a href={props.contacts.vk_url}><i className="fa fa-vk" aria-hidden="true"></i></a>
                <a href={props.contacts.whats_app_phone}><i className="fa fa-whatsapp" aria-hidden="true"></i></a>
                
                <NavLink to='/cart' className="app-mid-header-cart position-relative d-inline-flex">
                    <i className="fa fa-shopping-cart" aria-hidden="true">
                        <span className="app-mid-header-cart-count d-flex align-items-center justify-content-center">
                            {cookie.product == null ? <span> 0 </span>: <span> {cookie.product.length} </span>}
                        </span>
                    </i>
                </NavLink>
            </div>
	    </nav>
    );
}

export default HeaderMid;