import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import EventDetail from './EventDetail';
import { getEventDetailThunk, getEventProductThunk } from '../../redux/detail-reducer';
import { withRouter } from 'react-router-dom';
import Preloader from '../../common/Preloader';


const EventDetailContainer = (props) => {
    
    useEffect( () => {
        let eventID = props.match.params.eventID;
        props.getEventDetailThunk(eventID);
        props.getEventProductThunk(eventID);

        

        
    }, [])

    return <EventDetail eventDetail = {props.eventDetail}
                        eventProduct = {props.eventProduct} />
}




let mapStateToProps = (state) => {
    return {
        eventDetail: state.detailPage.eventDetail,
        eventProduct: state.detailPage.eventProduct,
    }
}

let withDataUrlEvent = withRouter(EventDetailContainer);

export default connect(mapStateToProps, {
    getEventDetailThunk,
    getEventProductThunk,
   
}) (withDataUrlEvent)