import { productAPI } from "../api/api";

const SET_PRODUCT_LIST = 'SET_PRODUCT_LIST';
const SET_TOTAL_COUNT = 'SET_TOTAL_COUNT;'
const SET_MIN = 'SET_MIN'
const SET_MAX = 'SET_MAX';

const SET_CUURENT_PAGE = 'SET_CURRENT_PAGE';
const SET_PAGE_SIZE = 'SET_PAGE_SIZE';




let initalState = {
    products: [],
    count: null,
    currentPage: 1,
    pageSize: 12,
    min: null,
    max: null,

}

const productReducer = (state = initalState, action) => {
    switch (action.type) {
        case SET_CUURENT_PAGE:
            return {
                ...state,
                currentPage: action.currentPage
            }
        case SET_PAGE_SIZE:
            return {
                ...state,
                pageSize: action.pageSize
            }
        case SET_PRODUCT_LIST:
            return {
                ...state,
                products: action.products
            }
        case SET_TOTAL_COUNT:
            return {
                ...state,
                count: action.count
            }
        case SET_MIN:
            return {
                ...state,
                min: action.min
            }
        case SET_MAX:
            return {
                ...state,
                max: action.max
            }

        default:
            return state;
    }
}


const setProductList = (products) => ({ type: SET_PRODUCT_LIST, products });

const setTotalCount = (count) => ({ type: SET_TOTAL_COUNT, count });
export const setCurrentPage = (currentPage) => ({ type: SET_CUURENT_PAGE, currentPage });
export const setPageSize = (pageSize) => ({ type: SET_PAGE_SIZE, pageSize });


const setMin = (min) => ({ type: SET_MIN, min });
const setMax = (max) => ({ type: SET_MAX, max });



export const getProductThunk = (cat, subID, brands, materials, sex, volumes, radius, powers, min, max, currentPage) => (dispatch) => {

    
    productAPI.getProduct(cat, subID, brands, materials, sex, volumes, radius, powers, min, max, currentPage).then(data => {
        
        dispatch(setMin(data.min));
        dispatch(setMax(data.max));
        dispatch(setTotalCount(data.count));
        dispatch(setProductList(data.results));
        

        
    });
}


export default productReducer;