import { contentAPI } from "../api/api";

const SET_CONTENT_SLIDE = 'SET_CONTENT_SLIDE';

const SET_PRODUCT_NEW = 'SET_PRODUCT_NEW';
const SET_PRODUCT_LEADER = 'SET_PRODUCT_LEADER';
const SET_PRODUCT_DISCOUNT = 'SET_PRODUCT_DISCOUNT';

const SET_IS_LOADING = 'SET_IS_LOADING';


let initalState = {
    slides: [],

    news: [],
    leaders: [],
    discounts: [],

    isLoad: false,
}

const contentReducer = (state = initalState, action) => {
    
    switch (action.type) {
        case SET_CONTENT_SLIDE:
            return {
                ...state,
                slides: action.slides
            }
        case SET_PRODUCT_NEW:
            return {
                ...state,
                news: action.news
            }
        case SET_PRODUCT_LEADER:
            return {
                ...state,
                leaders: action.leaders
            }
        case SET_PRODUCT_DISCOUNT:
            return {
                ...state,
                discounts: action.discounts
            }
        case SET_IS_LOADING:
            return {
                ...state,
                isLoad: action.isLoad
            }
        

        default:
            return state;
    }
}
const setSlides = (slides) => ({type: SET_CONTENT_SLIDE, slides});

const setNewProducts = (news) => ({type: SET_PRODUCT_NEW, news});
const setLeaderProducts = (leaders) => ({type: SET_PRODUCT_LEADER, leaders});
const setDiscountProducts = (discounts) => ({type: SET_PRODUCT_DISCOUNT, discounts});

const setIsLoad = (isLoad) => ({ type: SET_IS_LOADING, isLoad });






export const getSlideThunk = () => {
    return (dispatch) => {
        contentAPI.getContentSlide().then(data => {
            dispatch(setSlides(data));
            
        })
    }
}

export const getNewProductsThunk = () => {
    return (dispatch) => {
        dispatch(setIsLoad(true));
        contentAPI.getNewProducts().then(data => {
            dispatch(setNewProducts(data));
            dispatch(setIsLoad(false));
        })
    }
}

export const getLeaderProductsThunk = () => {
    return (dispatch) => {
        contentAPI.getLeaderProducts().then(data => {
            
            dispatch(setLeaderProducts(data));
        })
    }
}

export const getDiscountProductsThunk = () => {
    
    return (dispatch) => {
        dispatch(setIsLoad(true));
        contentAPI.getDiscountProducts().then(data => {
            dispatch(setDiscountProducts(data));
            dispatch(setIsLoad(false));
        })
    }
}

export default contentReducer;

