import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { getProductDetailThunk } from '../../redux/detail-reducer';
import Detail from './Detail';






const DetailContainer = (props) => {

    useEffect( () => {
        let detailID = props.match.params.detailID;
        props.getProductDetailThunk(detailID);

    }, [])
    return <Detail detail = {props.detail}
                   glasses = {props.glasses}
                   frames = {props.frames}
                   outlets = {props.outlets}
                   lang = {props.lang}
                   />
}

let mapStateToProps = (state) => {
    return {
        detail: state.detailPage.detail,
        lang: state.appPage.lang,
        glasses: state.appPage.glasses,
        frames: state.appPage.frames,
        outlets: state.appPage.outlets,
        
    }
}

let withDataUrlDetail = withRouter(DetailContainer);

export default connect(mapStateToProps, {
    getProductDetailThunk,

}) (withDataUrlDetail);

