import React from 'react';
import "./FooterModule.css";
import Cart from './ScrollCart/Cart';
import { stockData } from '../../redux/data';
import { useCookies } from 'react-cookie';

const Footer = (props) => {

	const [ langu, setLangu ] = useCookies(['lang']);
	
    return (
	<>
		<Cart />

        <div class="footer">
			<ul class="footer-item">
				<li><a class="footer-link" href="#"> { langu.lang != null ? stockData[langu.lang].footer.text1: stockData[props.lang].footer.text1 } </a></li>
				<li><a class="footer-link" href="#">{ langu.lang != null ? stockData[langu.lang].footer.text2: stockData[props.lang].footer.text2 } </a></li>
				<li><a class="footer-link" href="#">{ langu.lang != null ? stockData[langu.lang].footer.text3: stockData[props.lang].footer.text3 } </a></li>
				<li><a class="footer-link" href="#">{ langu.lang != null ? stockData[langu.lang].footer.text4: stockData[props.lang].footer.text4 } </a></li>
			</ul>
			<ul class="footer-item">
				<li><a class="footer-link" href="#"> { langu.lang != null ? stockData[langu.lang].footer.text5: stockData[props.lang].footer.text5 }  </a></li>
				<li><a class="footer-link" href="#">{ langu.lang != null ? stockData[langu.lang].footer.text6: stockData[props.lang].footer.text6 } </a></li>
				<li><a class="footer-link" href="#">{ langu.lang != null ? stockData[langu.lang].footer.text7: stockData[props.lang].footer.text7 } </a></li>
				<li><a class="footer-link" href="#">{ langu.lang != null ? stockData[langu.lang].footer.text8: stockData[props.lang].footer.text8 } </a></li>
				<li><a class="footer-link" href="#">{ langu.lang != null ? stockData[langu.lang].footer.text9: stockData[props.lang].footer.text9 } </a></li>
			</ul>
			<ul class="footer-item">
				<li><a class="footer-link" href="#">{ langu.lang != null ? stockData[langu.lang].footer.text11: stockData[props.lang].footer.text10 } </a></li>
				<li><span class="footer-link"> {props.contacts.phone} </span></li>
				<li><span class="footer-link" href="#"> {props.contacts.email} </span></li>
				<li>
					<div class="social-networks">
						<a href={props.contacts.insta_url}><i class="fa fa-instagram" aria-hidden="true"></i></a>
						<a href={props.contacts.vk_url}><i class="fa fa-vk" aria-hidden="true"></i></a>
						<a href={props.contacts.whats_app_phone}><i class="fa fa-whatsapp" aria-hidden="true"></i></a>
					</div>
				</li>
			</ul>
		</div>
	</>
    );
}

export default Footer;