import * as axios from 'axios';


const instance = axios.create({
    baseURL: `http://195.210.47.31/`,
});


export const appAPI = {
    getNavbarGlasses () {
        return instance.get(`api/products/subcategories/?cat=glasses`).then(response => {
            return response.data;
        });
    },

    getNavbarFrames () {
        return instance.get(`api/products/subcategories/?cat=frames`).then(response => {
            return response.data;
        });
    },

    getNavbarOutlets () {
        return instance.get(`api/products/subcategories/?cat=outlet`).then(response => {
            return response.data;
        });
    },

    getCartProducts (carts) {
        return instance.get(`api/products/cart/list/?ids=${carts}`).then(response => {
            return response.data;
        });
    }
}

