export const productAPI = {
    getProduct (cat, subID, brands, materials, sex, volumes, radius, powers, min, max, currentPage) {
        
        return instance.get(`api/products/list/?cat=${cat}&subcat=${subID}&brand=${brands}&material=${materials}&sex=${sex}&volume=${volumes}&radius=${radius}&opt_power=${powers}&min=${min}&max=${max}&page=${currentPage}&pageSize=12`).then(response => {
            return response.data;
        });
    }   
}
export const orderAPI = {
    postOrderData(email, name, phone, adress, comment, products) {
        return fetch('http://195.210.47.31/api/order/', {
            method: 'POST',
            body: JSON.stringify({
                email: email,
                name: name,
                phone: phone,
                adress: adress,
                comment: comment,
                products: products,
            }),
            headers: {
                'Content-type': 'application/json',
            }
        })
    }
}

