import React from 'react';
import { connect } from 'react-redux';
import Cart from './Cart';
import  { getCartsThunk} from '../../redux/app-reducer';
import { useEffect } from 'react';
import { useCookies } from 'react-cookie';



const CartContainer = (props) => {

    const [ cookie, setCookie ] = useCookies(['product']);

    useEffect(() => {  
          
        if (cookie.product != null) {
            
            let list = [];
            cookie.product.map( u => {
                console.log(u.id);
                list.push(u.id);
            });
            props.getCartsThunk(list.toString());
        }
        else {
            
            props.getCartsThunk('');
        }
        
    }, []);
    return <Cart lang = {props.lang}
                 carts = {props.carts}
                 isLoadCart = {props.isLoadCart}
                 getCartsThunk = {props.getCartsThunk}
                />
}

let mapStateToProps = (state) => {
    return {
        lang: state.appPage.lang,
        carts: state.appPage.carts,
        isLoadCart: state.appPage.isLoadCart
        
    }
}

export default connect(mapStateToProps, {
    getCartsThunk,
   
})(CartContainer)