import React, {  useEffect, useState } from 'react';
import { NavLink } from 'react-router-dom';
import ItemProduct from './ItemProduct/ItemProduct';
import './ProductModule.css';
import { stockData } from '../../../redux/data';
import { useCookies } from 'react-cookie';



const Product = (props) => {
    
    const [ langu, setLangu ] = useCookies(['lang']);
    
    const [products, setProducts ] = useState(props.news);

    


    

    let listProducts = products.map( u => <ItemProduct id = {u.id}
                                                       title =  {u.title}
                                                       price = {u.price}
                                                       is_discount = {u.is_discount}
                                                       discount = {u.discount}
                                                       img_1 = {u.img_1}
                                                       is_new = {u.is_new}
                                                       is_leader = {u.is_leader}
                                                       cat = {u.cat}
                                                       subcat = {u.subcat}
                                                       getCartsThunk = {props.getCartsThunk}
                                                        />);

    

    return (
        <>
            <div class="container text-center p-5 products-buttons-list">
                <span>{ langu.lang != null ? stockData[langu.lang].ourProduct.titleProduct:  stockData[props.lang].ourProduct.titleProduct}  </span>

                <hr />
                <div class="container product-buttons">
                    <NavLink to = "/" onClick = { () =>  setProducts(props.news) } > { langu.lang != null ? stockData[langu.lang].ourProduct.newProduct:  stockData[props.lang].ourProduct.newProduct} </NavLink>
                    <NavLink to = "/" onClick = { () => setProducts(props.leaders) } > { langu.lang != null ? stockData[langu.lang].ourProduct.newHits:  stockData[props.lang].ourProduct.newHits} </NavLink>
                    <NavLink to = "/" onClick = { () => setProducts(props.discounts) }> { langu.lang != null ? stockData[langu.lang].ourProduct.newSale:  stockData[props.lang].ourProduct.newSale} </NavLink>
                </div>
                <hr />
                
            </div>

            {products.length != 0 ? <div className = "container mx-auto">
                <div className = "row">
                    { listProducts }
                </div>
            </div>: <div className = "container text-center mb-5"> Jok productar </div>}
        </>
        
    );
}

export default Product;