import React from 'react';
import { useEffect } from 'react';
import { connect } from 'react-redux';
import Delivery from './Delivery';
import { getDeliveriesThunk } from '../../redux/contact-reducer';


const DeliveryContainer = (props) => {

    useEffect( () => {
        props.getDeliveriesThunk();
    }, []);

    return <Delivery lang = {props.lang} deliveries = {props.deliveries} />
}

let mapStateToProps = (state) => {
    return {
        lang: state.appPage.lang,
        deliveries: state.contactPage.deliveries,
    }
}

export default connect(mapStateToProps, {
    getDeliveriesThunk
})(DeliveryContainer)