import React, { useEffect } from 'react';
import Filter from './Filter/Filter';
import Subproduct from './Subproduct/Subproduct';
import './ProductModule.css';
import FilterContainer from './Filter/FilterContainer';
import Preloader from '../../common/Preloader';
import Pagination from './Pagination/Pagination';
import { NavLink } from 'react-router-dom';



const Product = (props) => {

   
    if (props.max == null || props.min == null) {
        return <Preloader />
    }
        
    let subproductList = props.products.map( u => <Subproduct id = {u.id}
                                                              title = {u.title}
                                                              price = {u.price}
                                                              is_discount = {u.is_discount}
                                                              discount = {u.discount}
                                                              img_1 = {u.img_1}
                                                              getCartsThunk = {props.getCartsThunk} />);
    return (
        <div class="app-content-base p-5">
            <div class="row">
                <div className="col-md-3">
                     <FilterContainer cat = {props.cat} 
                                      subName = {props.subName} 
                                      getProductThunk = {props.getProductThunk}
                                      subID = {props.subID}
                                      min = {props.min}
                                      max = {props.max}
                                      currentPage = {props.currentPage}
                                      lang = {props.lang} />
                </div>

                <div className = "col-md-9">
                    <div class="app-product-url">
				        {props.cat.toUpperCase()}  &raquo; {props.subName }
			        </div>

                    <div className = 'row'>
                        { subproductList }
                    </div>

                    <Pagination  count = { props.count }
                                 currentPage = { props.currentPage }
                                 pageSize = { props.pageSize }
                                 min = {props.min}
                                 subID = {props.subID}
                                 cat = {props.cat} 
                                 max = {props.max}
                                 getProductThunk = {props.getProductThunk}
                                 setCurrentPage = { props.setCurrentPage }
                                 lang = {props.lang} />
                </div>
            </div>
        </div>
    );
}

export default Product;