import React from 'react';
import { NavLink } from 'react-router-dom';
import './HeaderBottomModule.css';
import { useCookies } from 'react-cookie';
import { stockData } from '../../../redux/data';





const HeaderBottom = (props) => {
    const [ langu, setLangu ] = useCookies(['lang']);

    let glassesList = [];
    let framesList = [];
    let outletsList = [];

    props.glasses.map(u => {
        glassesList.push(<li> <a href = {`/product/glasses/${u.id}`} className="dropdown-item"> {u.value} </a> </li>);
    });

    props.frames.map(u => {
        framesList.push(<li><a href = {`/product/frames/${u.id}`}  className="dropdown-item"> {u.value} </a></li>);
    });

    props.outlets.map(u => {
        outletsList.push(<li><a href = {`/product/outlets/${u.id}`}  className="dropdown-item"> {u.value} </a></li>);
    });

    return (
        <nav class="navbar navbar-expand-lg menu-navbar">
            <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#main_nav">
                {langu.lang != null ? stockData[langu.lang].navbar.btnMenu: stockData[props.lang].navbar.btnMenu}
                <span class="navbar-toggler-icon"></span>
            </button>
	        <div class="collapse navbar-collapse" id="main_nav">
	            <ul class="navbar-nav">
		            <li class="nav-item dropdown">
		                <a  class="nav-link  dropdown-toggle" href="{% url 'product' %}" data-toggle="dropdown"> 
                        {langu.lang != null ? stockData[langu.lang].navbar.btnGlass: stockData[props.lang].navbar.btnGlass} 
                        </a>
		                <ul class="dropdown-menu">

                            {/* NAVBAR GLASSES SUBCAT LIST */}
                            { glassesList }

                        </ul>
		            </li>
                    <li class="nav-item dropdown">
                    <a class="nav-link  dropdown-toggle" href="{% url 'product' %}" data-toggle="dropdown">
                            {langu.lang != null ? stockData[langu.lang].navbar.btnFrame: stockData[props.lang].navbar.btnFrame}  
                        </a>
                        <ul class="dropdown-menu">
                            
                            {/* NAVBAR FRAMES SUBCAT LIST */}
                            { framesList }

                        </ul>
                    </li>
                    <li class="nav-item dropdown">
                    <a class="nav-link  dropdown-toggle" href="{% url 'product' %}" data-toggle="dropdown">
                        {langu.lang != null ? stockData[langu.lang].navbar.btnOutlet: stockData[props.lang].navbar.btnOutlet} 
                    </a>
                        <ul class="dropdown-menu">
                            
                            {/* NAVBAR OUTLETS SUBCAT LIST */}
                            { outletsList }

                        </ul>
                    </li>
                    <li class="nav-item"> <a class="nav-link" href = '/aboutUs' > {langu.lang != null ? stockData[langu.lang].navbar.btnAbout: stockData[props.lang].navbar.btnAbout}   </a> </li>
                    <li class="nav-item"><a class="nav-link" href = '/salon' > {langu.lang != null ? stockData[langu.lang].navbar.btnSalon: stockData[props.lang].navbar.btnSalon} </a></li>
                    <li class="nav-item"><a class="nav-link" href = '/delivery' > {langu.lang != null ? stockData[langu.lang].navbar.btnDelivery: stockData[props.lang].navbar.btnDelivery} </a></li>
		
	            </ul>
	        </div> 
	    </nav>
    );
}


export default HeaderBottom;