import { act } from "react-dom/test-utils";
import { appAPI } from "../api/api";

const SET_NAVBAR_GLASSES = 'SET_NAVBAR_GLASSES';
const SET_NAVBAR_FRAME = 'SET_NAVBAR_FRAME';
const SET_NAVBAR_OUTLET = 'SET_NAVBAR_OUTLET';

const SET_LANG_WEBSITE = 'SET_LANG_WEBSITE';
const SET_CART_PRODUCT = 'SET_CART_PRODUCT';

const SET_IS_LOAD_CART = 'SET_IS_LOAD_CART';



let initialState = {
    glasses: [],
    frames: [],
    outlets: [],
    carts: [],
    lang: 'RU',

    isLoadCart: false,
}
const appReducer = (state = initialState, action) => {
   
    switch (action.type) {
        case SET_NAVBAR_GLASSES:
            return {
                ...state,
                glasses: action.glasses
            }
        case SET_NAVBAR_FRAME:
            return {
                ...state,
                frames: action.frames
            }
        case SET_NAVBAR_OUTLET:
            return {
                ...state,
                outlets: action.outlets
            }
        case SET_LANG_WEBSITE:
            return {
                ...state,
                lang: action.lang
            }
        case SET_CART_PRODUCT:
            return {
                ...state,
                carts: action.carts

            }
        case SET_IS_LOAD_CART:
            return {
                ...state,
                isLoadCart: action.isLoadCart
            }
        default:
            return state;
    }
}

export const setNavbarGlasses = (glasses) => ({type: SET_NAVBAR_GLASSES, glasses});
export const setNavbarFrames = (frames) => ({type: SET_NAVBAR_FRAME, frames});
export const setNavbarOutlets = (outlets) => ({type: SET_NAVBAR_OUTLET, outlets});
export const setCartProducts = (carts) => ({type: SET_CART_PRODUCT, carts});

export const setIsLoadCart = (isLoadCart) => ({ type: SET_IS_LOAD_CART, isLoadCart });


export const setLangWebsite = (lang) => ( {type: SET_LANG_WEBSITE, lang} );




export const getGlassesThunk = () => {
    return (dispatch) => {
        appAPI.getNavbarGlasses().then(data => {
            dispatch(setNavbarGlasses(data));
        });
    } 
}

export const getFramesThunk = () => {
    return (dispatch) => {
        appAPI.getNavbarFrames().then(data => {
            dispatch(setNavbarFrames(data));
        });
    }
}

export const getOutletsThunk = () => {
    return (dispatch) => {
        appAPI.getNavbarOutlets().then(data => {
            dispatch(setNavbarOutlets(data));
        });
    }
}
export const getCartsThunk = (carts) => {

    return (dispatch) => {
        appAPI.getCartProducts(carts).then(data => {
            dispatch(setCartProducts(data));
        });
    }
}



export default appReducer;







