import React from 'react';
import './CartModule.css';
import { stockData } from '../../redux/data';
import { useCookies } from 'react-cookie';
import CartItem from './CartItem/CartItem';
import { NavLink } from 'react-router-dom';
import PreloaderForPage from '../../common/PreloaderForPage';



const Cart = (props) => {

	const [ cookie, setCookie ] = useCookies(['product']);
	const [ counter, setCounter ] = useCookies(['count']);
	const [ langu, setLangu ] = useCookies(['lang']);

	if ( props.carts.length == 0 ) {
		return <PreloaderForPage title = { langu.lang != null ? stockData[langu.lang].cart.title: stockData[props.lang].cart.title } />
	}

	

	let list = cookie.product;
	let totalCount = 0;
	list.map((u, ides) => {
		let price = props.carts[ides].is_discount ? (props.carts[ides].price * (1 - (props.carts[ides].discount / 100))):props.carts[ides].price;
		totalCount += price * parseInt(u.count);
	}
		
	);

	const saveCountCookie = () => {
		setCounter('count', totalCount, {path: '/'});
	}

	let cartsList = list.map ( (u, ides) => <CartItem id = {props.carts[ides].id}
													  title = {props.carts[ides].title}
													  price = {props.carts[ides].price}
													  discount = {props.carts[ides].discount}
													  img_1 = {props.carts[ides].img_1}
													  is_discount = {props.carts[ides].is_discount}
													  count = { u.count }
													  getCartsThunk = {props.getCartsThunk}
													  idCookies = { ides } />)

    return (
        <div class="container">
		<div class="app-cart">
			

			<div class="app-cart-title text-center">
                <span> { langu.lang != null ? stockData[langu.lang].cart.title: stockData[props.lang].cart.title }</span>
			</div>

			<hr class="app-cart-hr" />

			<div class="row app-cart-rows">

				{ cartsList }

				
			</div>

			
			{list.length != 0 &&  <div class="app-cart-fixed d-flex justify-content-between">

				
					<a href = {'/'} class = "btn app-cart-footer-prev">
						{ langu.lang != null ? stockData[langu.lang].cart.prev: stockData[props.lang].cart.prev }
					</a>
				
				


				<div class="app-cart-footer-total">
					{ langu.lang != null ? stockData[langu.lang].cart.total: stockData[props.lang].cart.total } {totalCount},00 KZT
				</div>

				
					
					<NavLink to = {'/cart/order'} class = "btn app-cart-footer-next" onClick = { saveCountCookie }>
						{ langu.lang != null ? stockData[langu.lang].cart.submit: stockData[props.lang].cart.submit }
					</NavLink>
					
				
			</div>}
			

		</div>
	</div>
    );
}

export default Cart;