import React from 'react';
import PreloaderForUs from '../../common/PreloaderForUs';
import { stockData } from '../../redux/data';
import './AboutUsModule.css';
import { useCookies } from 'react-cookie';



const AboutUs = (props) => {

    const [ langu, setLangu ] = useCookies(['lang']);

    if (props.aboutUs == null) {
        return <PreloaderForUs title = {langu.lang != null ? stockData[langu.lang].about.title : stockData[props.lang].about.title} />
    }
    
    return (
        <div class="container py-5">
           
            
            <div class = "app-about-us">
                <div className = 'app-delivery-title text-center'>
                    <span class="text-center"> {langu.lang != null ? stockData[langu.lang].about.title : stockData[props.lang].about.title} </span>
                </div>

                <hr class="app-delivery-hr" />

                <div className = "app-about-us-description">
                    {props.aboutUs}
                </div>
            </div>
	    </div>
    );
}


export default AboutUs;