import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { getSlideThunk, getNewProductsThunk, getLeaderProductsThunk, getDiscountProductsThunk  } from '../../redux/content-reducer';
import Content from './Content';



const ContentContainer = (props) => {

    useEffect( () => {
        props.getSlideThunk();
        props.getNewProductsThunk();
        props.getLeaderProductsThunk();
        props.getDiscountProductsThunk();
    }, []);

    return <Content slides = {props.slides}
                    news = {props.news}
                    leaders = {props.leaders}
                    discounts = {props.discounts}
                    isLoad = {props.isLoad}
                    lang = {props.lang} />
}


let mapStateToProps = (state) => {
    return {
        slides: state.contentPage.slides,
        news: state.contentPage.news,
        leaders: state.contentPage.leaders,
        discounts: state.contentPage.discounts,
        isLoad: state.contentPage.isLoad,
        lang: state.appPage.lang,
    }
}

export default connect(mapStateToProps, {
    getSlideThunk,
    getNewProductsThunk,
    getLeaderProductsThunk,
    getDiscountProductsThunk,
})(ContentContainer);


