import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import Preloader from '../../../common/Preloader';
import  { getBrandsThunk, getMaterailsThunk, getRadiusThunk, getVolumesThunk, getPowersThunk } from '../../../redux/filter-reducer';
import Filter from './Filter';


const FilterContainer = (props) => {
    
    useEffect( () => {
        
        let cat = props.match.params.cat;
        props.getBrandsThunk(cat);
        props.getMaterailsThunk(cat);
        props.getPowersThunk(cat);
        props.getRadiusThunk(cat);
        props.getVolumesThunk(cat);
        


    }, [])

    

    return <Filter brands = {props.brands}
                   materials = {props.materials}
                   sex = {props.sex}
                   radius = {props.radius}
                   volumes = {props.volumes}
                   powers = {props.powers}
                   cat = {props.cat}
                   subName = {props.subName}
                   subID = {props.subID}
                   getProductThunk = {props.getProductThunk}
                   min = { props.min }
                   max = { props.max }
                   currentPage = { props.currentPage }
                   lang = {props.lang}
                    />
}


let mapStateToProps = (state) => {
    return {

        brands: state.filterPage.brands,
        materials: state.filterPage.materials,
        sex: state.filterPage.sex,
        radius: state.filterPage.radius,
        volumes: state.filterPage.volumes,
        powers: state.filterPage.powers,


        

    }
}

let withDataUrlFilter = withRouter(FilterContainer);

export default connect(mapStateToProps, {
    getBrandsThunk,
    getMaterailsThunk,
    getRadiusThunk,
    getVolumesThunk,
    getPowersThunk,
})(withDataUrlFilter);