import React, { useState } from 'react';
import './FilterModule.css';
import Slider from '@material-ui/core/Slider';
import { createMuiTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';
import { stockData } from '../../../redux/data';
import { useCookies } from 'react-cookie';





export let brand = [];
export let material = [];
export let sex = [];

export let radius = [];
export let volumes = [];
export let powers = [];



const Filter = (props) => { 
    console.log("FILTER PAGE");

    const [ langu, setLangu ] = useCookies(['lang']);
    const [value, setValue] = useState([props.min, props.max]);

   

    const handleChange = (event, newValue) => {
        setValue(newValue);
    }
    const UnCheck = () => {
        var inputs = document.getElementsByTagName ("input");

        for(let i = 0; i < inputs.length; i++) {
           inputs[i].checked = false;
        }
        setValue([props.min, props.max]);
        props.getProductThunk(props.cat, props.subID,  '',
                                                        '', 
                                                        '', 
                                                        '', 
                                                        '',
                                                        '',
                                                        props.min, props.max, 1);


    }

    

    

    const handleFetch = (event, newValue) => {
        props.getProductThunk(props.cat, props.subID,  brand.toString(),
                                                        material.toString(), 
                                                        sex.toString(), 
                                                        volumes.toString(), 
                                                        radius.toString(),
                                                        powers.toString(),
                                                        newValue[0], newValue[1], 1);
    }
   

    const muiTheme = createMuiTheme({
        overrides:{
            MuiSlider: {
                thumb:{
                    color: '#AF4C4C',
                },
                track: {
                    color: '#AF4C4C',
                },
                rail: {
                    color: 'black'
                }      
            }
        }
    });

    
    
    const onRequest = () => {
        props.getProductThunk(props.cat, props.subID,  brand.toString(),
                                                        material.toString(), 
                                                        sex.toString(), 
                                                        volumes.toString(), 
                                                        radius.toString(),
                                                        powers.toString(),
                                                        value[0], value[1], 1);
    }

    const onClickBrand = (e) => {
        if (e.target.checked) {
            brand.push(e.target.value);
            onRequest(); 
        }
        else {
            brand = brand.filter(u => u != e.target.value);
            onRequest();
            
        }
    }

    const onClickedMaterial = (e) => {
        if (e.target.checked) {
            material.push(e.target.value);
            
            onRequest();
        }
        else {
            material = material.filter(u => u != e.target.value);
            
            onRequest();
        }
    }

    const onClickSex = (e) => {
        if (e.target.checked) {
            sex.push(e.target.value);
            onRequest();
        }
        else {
            sex = sex.filter(u => u != e.target.value);
            onRequest();
        }
    }

    const onClickRadius = (e) => {
        if (e.target.checked) {
            radius.push(e.target.value);
            onRequest();
        }
        else {
            radius = radius.filter(u => u != e.target.value);
            onRequest();
        }
    }

    const onClickVolume = (e) => {
        if (e.target.checked) {
            volumes.push(e.target.value);
            onRequest();

        }
        else {
            volumes = volumes.filter(u => u != e.target.value);
            onRequest();
        }
    }

    const onClikcPower = (e) => {
        if (e.target.checked) {
            powers.push(e.target.value);
            onRequest();
        }
        else {
            powers = powers.filter(u => u != e.target.value);
            onRequest();
        }
    }

    
    


    let brandTags = props.brands.map (u => <label class="container_checkbox"> {u.name} 
                                                <input id = "1" type="checkbox" onClick = { onClickBrand }  value = {u.name} />
                                                <span class="checkmark"> </span>
                                            </label>);

    
    let materialTags = props.materials.map(u => <label class="container_checkbox"> {u.name}
                                                    <input id = "1" type="checkbox" onClick = { onClickedMaterial }  value = {u.name}/>
                                                    <span class="checkmark"> </span>
                                                </label>);


    let sexTags = props.sex.map(u => <label class="container_checkbox"> {u.name} 
                                        <input id = "1" type="checkbox" onClick = { onClickSex } value = {u.name}/>
                                        <span class="checkmark"> </span>
                                    </label>);


    let volumeTags = props.volumes.map(u => <label class="container_checkbox"> {u.name}
                                                <input id = "1" type="checkbox" onClick = { onClickVolume } value = {u.name}/>
                                                <span class="checkmark"> </span>
                                            </label>);

    
    let radiusTags = props.radius.map(u => <label class="container_checkbox"> {u.name}
                                                <input id = "1" type="checkbox" onClick = { onClickRadius } value = {u.name}/>
                                                <span class="checkmark"> </span>
                                            </label>);


    let powerTags = props.powers.map(u => <label class="container_checkbox"> {u.name} 
                                                <input id = "1" type="checkbox" onClick = { onClikcPower } value = {u.name}/>
                                                <span class="checkmark"> </span>
                                            </label>);
    
    
    

    return (
                    <div class="app-filter">
                            <div class="app-filter-name text-center">
                                <span> {props.subName} </span>
                            </div>

                            <div class="app-filter-padding">
                                <div class="app-filter-price">
                                    {langu.lang != null ? stockData[langu.lang].filter.price: stockData[props.lang].filter.price}
                                </div>
                                <div class="app-filter-range">
                                    <ThemeProvider theme = {muiTheme}>
                                        <Slider
                                            onChangeCommitted = {handleFetch}
                                            value = {value}
                                            onChange = {handleChange}
                                            
                                            valueLabelDisplay="auto"
                                            aria-labelledby="range-slider"
                                            min = { props.min }
                                            max = { props.max }
                                            /> 
                                    </ThemeProvider>
                                </div>
                            </div>
                        


                        {/* BRAND */}
                        {props.brands.length != 0 && <div id="accordionExample">
                            <div>
                                <div id="heading" class = 'app-filter-title d-flex'>
                                    <div class="btn btn-block text-left" type="button" data-toggle="collapse" data-target="#brandCollapce" aria-controls="brandCollapce">
                                        <span class = 'app-filter-subtitle'>{langu.lang != null ? stockData[langu.lang].filter.brand: stockData[props.lang].filter.brand} </span>
                                    </div>
                                </div>     
                            </div>

                            <div id="brandCollapce" class="collapse" aria-labelledby="heading" data-parent="#accordionExample">

                                { brandTags }
                            </div>
                        </div>}

                        {/* MATERIAL */}
                        {props.materials.length != 0 &&  <div id="accordionExample">
                            <div>
                                <div id="heading1" class = 'app-filter-title d-flex'>
                            
                                    <div class="btn btn-block text-left" type="button" data-toggle="collapse" data-target="#collapseOne1" aria-controls="collapseOne">
                                        <span class = 'app-filter-subtitle'> {langu.lang != null ? stockData[langu.lang].filter.material: stockData[props.lang].filter.material} </span>
                                    </div>

                                </div>     
                            </div>

                            <div id="collapseOne1" class="collapse" aria-labelledby="heading1" data-parent="#accordionExample">

                                { materialTags }
                            </div>
                        </div> }


                        {/* SEX */}
                        {(props.cat == 'glasses' || props.cat == 'outlets') && <div id="accordionExample">
                            <div>
                                <div id="headingOne2" class = 'app-filter-title d-flex'>
                            
                                    <div class="btn btn-block text-left" type="button" data-toggle="collapse" data-target="#collapseOne2" aria-controls="collapseOne">
                                        <span class = 'app-filter-subtitle'> {langu.lang != null ? stockData[langu.lang].filter.sex: stockData[props.lang].filter.sex} </span>
                                    </div>

                                </div>     
                            </div>

                            <div id="collapseOne2" class="collapse" aria-labelledby="headingOne2" data-parent="#accordionExample">

                                { sexTags }
                            </div>
                        </div>}

                        {/* AREA */}

                        {props.volumes.length != 0 && <div id="accordionExample">
                            <div>
                                <div id="headingOne3" class = 'app-filter-title d-flex'>
                            
                                    <div class="btn btn-block text-left" type="button" data-toggle="collapse" data-target="#collapseOne3" aria-controls="collapseOne">
                                        <span class = 'app-filter-subtitle'> {langu.lang != null ? stockData[langu.lang].filter.area: stockData[props.lang].filter.area} </span>
                                    </div>

                                </div>     
                            </div>

                            <div id="collapseOne3" class="collapse" aria-labelledby="headingOne3" data-parent="#accordionExample">

                                { volumeTags }
                            </div>
                        </div>}


                        {/* OPTIC POWER */}

                        {props.powers.length != 0 && <div id="accordionExample">
                            <div>
                                <div id="headingOne4" class = 'app-filter-title d-flex'>
                            
                                    <div class="btn btn-block text-left" type="button" data-toggle="collapse" data-target="#collapseOne4" aria-controls="collapseOne">
                                        <span class = 'app-filter-subtitle'> {langu.lang != null ? stockData[langu.lang].filter.power: stockData[props.lang].filter.power} </span>
                                    </div>

                                </div>     
                            </div>

                            <div id="collapseOne4" class="collapse" aria-labelledby="headingOne4" data-parent="#accordionExample">

                                { powerTags }
                            </div>
                        </div>}


                        {/* RADIUS */}
                        {props.radius.length != 0 && <div id="accordionExample">
                            <div>
                                <div id="headingOne5" class = 'app-filter-title d-flex'>
                            
                                    <div class="btn btn-block text-left" type="button" data-toggle="collapse" data-target="#collapseOne5" aria-controls="collapseOne">
                                        <span class = 'app-filter-subtitle'> {langu.lang != null ? stockData[langu.lang].filter.radius: stockData[props.lang].filter.radius} </span>
                                    </div>

                                </div>     
                            </div>

                            <div id="collapseOne5" class="collapse" aria-labelledby="headingOne5" data-parent="#accordionExample">

                                { radiusTags }

                            </div>
                        </div>}


                        <div class="app-filter-clear" onClick = { UnCheck }>
                            
                            <button type="button" class="btn app-filter-clear-button">{langu.lang != null ? stockData[langu.lang].filter.button: stockData[props.lang].filter.button}</button>
                            
                        </div>
                    </div>

    );
}

export default Filter;