import { contactAPI } from "../api/api";

const SET_DELIVERIES = 'SET_DELIVERIES';
const SET_ABOUT_US_TEXT = 'SET_ABOUT_US_TEXT';
const SET_EMAIL_AND_PHONE = 'SET_EMAIL_AND_PHONE';

let initalState = {
    deliveries: [],
    aboutUs: null,
    contacts: [],
}

const contactReducer = (state = initalState, action) => {
    switch (action.type) {
        case SET_DELIVERIES:
            return {
                ...state,
                deliveries: action.deliveries
            }
        case SET_ABOUT_US_TEXT:
            return {
                ...state,
                aboutUs: action.aboutUs
            }
        case SET_EMAIL_AND_PHONE:
            return {
                ...state,
                contacts: action.contacts
            }
        default:
            return state;
    }
}

const setDeliveries = (deliveries) => ({ type: SET_DELIVERIES, deliveries });
const setAboutUs = (aboutUs) => ({ type: SET_ABOUT_US_TEXT, aboutUs });
const setContacts = (contacts) => ({ type: SET_EMAIL_AND_PHONE, contacts });

export const getDeliveriesThunk = () => (dispatch) => {
    contactAPI.getDeliveries().then(data => {
        dispatch(setDeliveries(data));
    })
}
export const getAboutUsThunk = () => (dispatch) => {
    contactAPI.getAboutUs().then(data => {
        dispatch(setAboutUs(data.text));
    })
}
export const getContactsThunk = () => (dispatch) => {
    contactAPI.getContacts().then(data => {
        dispatch(setContacts(data));
    })
}

export default contactReducer;