import React from 'react';
import "./InfoModule.css";
import { stockData } from '../../../redux/data';
import { useCookies } from 'react-cookie';



const Info = (props) => {

	const [ langu, setLangu ] = useCookies(['lang']);
	
    return (
        <nav class="navbar navbar-expand-lg p-0 m-0">
            <div class= "about-us-navbar">

            	<div class="about-us-item">
            		<i class="fa fa-building" aria-hidden="true"></i>
            		<p> {langu.lang != null ? stockData[langu.lang].info.info1: stockData[props.lang].info.info1} </p>
            	</div>

            	<div class="about-us-item">
            		<i class="fa fa-headphones" aria-hidden="true"></i>
            		<p>{langu.lang != null ? stockData[langu.lang].info.info2: stockData[props.lang].info.info2} </p>
            	</div>

            	<div class="about-us-item">
            		<i class="fa fa-birthday-cake" aria-hidden="true"></i>
            		<p> {langu.lang != null ? stockData[langu.lang].info.info3: stockData[props.lang].info.info3} </p>
            	</div>

            	<div class="about-us-item">
            		<i class="fa fa-shield" aria-hidden="true"></i>
            		<p> {langu.lang != null ? stockData[langu.lang].info.info4: stockData[props.lang].info.info4} </p>
            	</div>

            	<div class="about-us-item">
            		<i class="fa fa-product-hunt" aria-hidden="true"></i>
            		<p> {langu.lang != null ? stockData[langu.lang].info.info5: stockData[props.lang].info.info5} </p>
            	</div>   

            </div>
        </nav>
    );
}

export default Info;