import { ButtonGroup } from '@material-ui/core';
import React from 'react';
import './OrderModule.css';
import { Field, reduxForm } from 'redux-form';
import { useCookies } from 'react-cookie';
import { stockData } from '../../redux/data';




const OrderForm = (props) => {

   

    return (
        <form onSubmit = { props.handleSubmit }>
                <div className = 'row'>

                    <div className = 'col-md-5'>

                        <div className = "app-order-input-title">
                            <span > {props.langu != null ? stockData[props.langu].order.user_title: stockData[props.lang].order.user_title} </span>
                        </div>
                        <div className = 'app-data-order'>
                            <div class="form-group">
                                <label for="exampleInputEmail1">* {props.langu != null ? stockData[props.langu].order.email: stockData[props.lang].order.email} </label>
                                <Field type="email" component = {'input'} className = 'form-control' name = {'email'} placeholder = " " />
                                {false && <small id="emailHelp" class="form-text text-muted">We'll never share your email with anyone else.</small>}
                            </div>

                            <div class="form-group">
                                <label for="formGroupExampleInput"> * {props.langu != null ? stockData[props.langu].order.name: stockData[props.lang].order.name}</label>
                                <Field type="text" component = {'input'} className="form-control" name = {'name'} placeholder = "g"  />
                                
                            </div>

                            <div class = "form-group">
                                <label for="phone">* {props.langu != null ? stockData[props.langu].order.phone: stockData[props.lang].order.phone}</label>
                                <Field type="tel"  component = {'input'} className="form-control" name={'phone'}  placeholder="+7(708) 417 19 75" />
                               
                            </div>
                        </div>
                    </div>
                    <div className = 'col-md-1'> </div>

                    <div className = 'col-md-6'>
                        <div className = "app-variant-order">
                            <div class="form-group">
                                <label for="formGroupExampleInput">{props.langu != null ? stockData[props.langu].order.address: stockData[props.lang].order.address} </label>
                                <Field type="text" component = {'input'} className="form-control"  placeholder=" " name = {'adress'} />
                                
                            </div>

                            <div class="form-group">
                                <label for="comment">{props.langu != null ? stockData[props.langu].order.comment: stockData[props.lang].order.comment}</label>
                                <Field  component = {'textarea'} class="form-control" rows="5" id="comment" name = {'comment'} />
                            </div>
                        </div>
                        
                    </div>
                </div>

                <div className = 'row app-order-counter p-1'>
                    <div className = 'col-lg-6 col-md-6 col-6 pt-2 text-center'>
                        <span className = "app-order-summa"> {props.langu != null ? stockData[props.langu].order.send_text: stockData[props.lang].order.send_text}: {props.totalPrice} KZT </span>
                    </div>
                    
                    <div className = 'col-lg-6  col-md-6 col-6 text-center p-1'>
                        
                        <button className = "btn btn-warning app-order-finish-button float-right"> {props.langu != null ? stockData[props.langu].order.send_button: stockData[props.lang].order.send_button}  </button>
                    </div>
                </div>
            </form>
    );
}

const OrderReduxForm = reduxForm({ form: 'order'})(OrderForm);

const Order = (props) => {

    
    const [ cookie, setCookie ] = useCookies(['product']);
    const [ counter, setCounter ] = useCookies(['count']);
    const [ langu, setLangu ] = useCookies(['lang']);
   

    
    const onSubmit = (formData) => {
        props.postOrderDataThunk(formData.email, formData.name, formData.phone, formData.adress, formData.comment, cookie.product);
    }


    return (
        <div className = 'app-order container'>
            <div className ='app-cart-title text-center'>
                <span> {langu.lang != null ? stockData[langu.lang].order.title: stockData[props.lang].order.title} </span>
            </div>

            <hr className = 'app-cart-hr' />
            
            <OrderReduxForm onSubmit = { onSubmit } lang = {props.lang} totalPrice = {counter.count} langu = {langu.lang} />


        </div>
    );
}






export default Order;