import React from 'react';
import preloader from '../assets/images/load2.svg';
import './PreloaderModule.css';


const PreloaderForPage = (props) => {


   
    return (
        <div class="container">
			<div class="app-cart text-center">
				<div class="app-cart-title text-center">
					<span> { props.title }</span>
				</div>

				<hr class="app-cart-hr" />
                <div className = 'app-load-div-for-page'>
                <img  src={preloader} />
                <h6>  ... </h6>: <div></div>
                </div>
			</div>
		</div>
    )
}

export default PreloaderForPage;