import React from 'react';
import { NavLink } from 'react-router-dom';
import { useState } from 'react';
import { useCookies } from 'react-cookie';




const CartItem = (props) => {

	const [count, setCount] = useState(props.count);
	const [ cookie, setCookie ] = useCookies(['product']);
	
	
	const combineById = (object) => {
		let list = object.map (u => u.id);
		return list;
	}

	const handleChange = (e) => {
		setCount(e.target.value);
		let products = cookie.product;
		products[props.idCookies].count = parseInt(e.target.value);
		setCookie('product', products, {path: '/'});

	}

    const deleteCookies = (event) => {
        let products = cookie.product;
        products = products.filter(u => u.id != event);	
		if (products.length  == 0) {
			
			props.getCartsThunk('');
		}
		else {
			let temp = combineById(products);
			props.getCartsThunk(temp.toString());
		}

        setCookie('product', products, {path: '/'});
	}
	
 
	
    return (
        <div class="col-xl-3 col-lg-4 col-md-6 mb-3">
			<div class="app-cart-div text-center">
				<NavLink to = {`product/${props.id}`}>
					<div class="app-product-image-div text-center">
						<img class = 'app-product-image' src={`http://s-optic.herokuapp.com${props.img_1}`} />
					</div>
				</NavLink>
				<NavLink to = {`product/${props.id}`}>
					<div class="app-cart-info">
						<span class="app-cart-name"> {props.title} </span>
					</div>
				</NavLink>

				<div class="app-cart-count-div p-2">
                    <input class="form-control form-control-sm w-50" onChange = { handleChange } type="number" min="1" value={count} />
				</div>

				<div class="app-cart-price m-2">
					{props.is_discount ? <span>  {props.price * (1 - (props.discount / 100 ))} KZT </span>:<span>  {props.price} KZT </span>}
				</div>

				<div class="app-cart-button-div mb-3 p-2" onClick = { () =>  deleteCookies(props.id) }>
					X
				</div>

			</div>
					
		</div>
    );
}


export default CartItem;