import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { getGlassesThunk, getFramesThunk, getOutletsThunk, setLangWebsite } from '../../redux/app-reducer';
import { getContactsThunk } from '../../redux/contact-reducer';
import Header from './Header';



const HeaderContainer = (props) => {

    useEffect( () => {
        props.getContactsThunk();
        props.getGlassesThunk();
        props.getFramesThunk();
        props.getOutletsThunk();    
    }, []);


    return <Header glasses = {props.glasses} 
                   frames = {props.frames}
                   outlets = {props.outlets}
                   lang = { props.lang } 
                   carts = { props.carts }
                   setLangWebsite = { props.setLangWebsite }
                   contacts = { props.contacts } />
}

let mapStateToProps = (state) => {
    return {
        glasses: state.appPage.glasses,
        frames: state.appPage.frames,
        outlets: state.appPage.outlets,
        lang: state.appPage.lang,
        carts: state.appPage.carts,
        contacts: state.contactPage.contacts,
    }
}

export default connect(mapStateToProps, {
    getGlassesThunk,
    getFramesThunk,
    getOutletsThunk,
    setLangWebsite,
    getContactsThunk,
})(HeaderContainer);