import React from 'react';
import HeaderBottom from './HeaderBottom/HeaderBottom';
import HeaderMid from './HeaderMid/HeaderMid';
import './HeaderModule.css';
import { useCookies } from 'react-cookie';






const Header = (props) => {
    
    const [ langu, setLangu ] = useCookies(['lang']);
    const handleChange = (e) => {
        setLangu('lang', e.target.value, {path: '/'});
        props.setLangWebsite(langu.lang);

    }

    

    
    return (
        <>
            <nav className="navbar navbar-expand-lg navbar-dark bg-dark app-top-header">
                <div className="d-flex justify-content-between w-100">
                    <ul className="navbar-nav mr-auto">
                        <li className="nav-item">
                            {}
                            <i className="nav-link fa fa-phone">  {props.contacts.phone} </i>
                        </li>
                        <li className="nav-item collapse navbar-collapse">
                            <i className="nav-link fa fa-envelope"> {props.contacts.email} </i>
                        </li>
                    </ul>
                    <div className="d-flex">
                        <select className="custom-select custom-select-sm" value = {langu.lang} onChange = { handleChange }>
                            <option value="RU">RU</option>
                            <option value="KZ">KZ</option>
                        </select>
                        <a href="/dashboard" className="app-top-header-admin">  <i className="fa fa-user-o" aria-hidden="true"></i>	</a>	
                    </div>
                </div>
            </nav>

            <HeaderMid  carts = {props.carts}
                        contacts = {props.contacts} />
            <HeaderBottom glasses = {props.glasses}
                          frames = {props.frames}
                          outlets = {props.outlets}
                          lang = {props.lang} />
        </>
    );
}

export default Header;