import React from 'react';
import './SlideModule.css';
import { NavLink } from 'react-router-dom';


const Slide = (props) => {

    
    let caruselIndicatorsList = [];
    let caruselInnerList = [];

    props.slides.map( (u, id) => {
        if (id == 0) {
            caruselIndicatorsList.push(<li data-target="#carouselExampleIndicators" data-slide-to={id} className='active'></li>);
            caruselInnerList.push(<div className='carousel-item active'> <NavLink to = {`/event/${u.id}`}><img src={u.img_slide} class="d-block w-100" alt="..." /></NavLink> </div>)
        }
        else {
            caruselIndicatorsList.push(<li data-target="#carouselExampleIndicators" data-slide-to={id}></li>);
            caruselInnerList.push(<div className='carousel-item'> <NavLink to ={`/event/${u.id}`}><img src={u.img_slide} class="d-block w-100" alt="..." /></NavLink> </div>)
        }
    });


    return (
        <div id="carouselExampleIndicators" class="carousel slide" data-ride="carousel">

            <ol class="carousel-indicators">

                { caruselIndicatorsList }
	        </ol>


            <div class="carousel-inner">

                { caruselInnerList }
	        </div>


            <a class="carousel-control-prev" href="#carouselExampleIndicators" role="button" data-slide="prev">
                <span class="carousel-control-prev-icon" aria-hidden="true"></span>
                <span class="sr-only">Previous</span>
	        </a>

            <a class="carousel-control-next" href="#carouselExampleIndicators" role="button" data-slide="next">
                <span class="carousel-control-next-icon" aria-hidden="true"></span>
                <span class="sr-only">Next</span>
	        </a>
	  
        </div>
    
    )
}

export default Slide;

