import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import  { getProductThunk, setPageSize, setCurrentPage } from '../../redux/product-reducer';
import  { getCartsThunk } from '../../redux/app-reducer';
import Product from '../Product/Product';



const ProductContainer = (props) => {
    
    let cat = props.match.params.cat;
    let subID = props.match.params.subcatID;

    

    useEffect( () => {
        
        props.getProductThunk(cat, subID, '', '', '', '', '', '', '', '', props.currentPage);

    }, [])


    //              FIND SUBCAT BY SUBID                    //
    const findSubcat = (subID, lists) => {
        return lists.map(u => subID == u.id ? u.value : '');
    }
    let subName = '';
    if (cat == 'glasses') {
        subName = findSubcat(subID, props.glasses);
    }

    else if (cat == 'frames') {
        subName = findSubcat(subID, props.frames);
    }
    
    else {
        subName = findSubcat(subID, props.outlets);
    }
    

    return <Product products = {props.products}
                    count = {props.count}
                    cat = {cat}
                    lang = {props.lang}
                    subName= {subName}
                    subID = {subID}
                    getProductThunk = {props.getProductThunk}
                    min = {props.min}
                    max = {props.max}
                    currentPage = { props.currentPage }
                    pageSize = { props.pageSize }
                    setCurrentPage = { props.setCurrentPage }
                    getCartsThunk = { props.getCartsThunk }
                    

                    
 />
}


let mapStateToProps = (state) => {
    return {
        products: state.productPage.products,
        count: state.productPage.count,
        glasses: state.appPage.glasses,
        lang: state.appPage.lang,
        frames: state.appPage.frames,
        outlets: state.appPage.outlets,
        min: state.productPage.min,
        max: state.productPage.max,
        currentPage: state.productPage.currentPage,
        


    }
}

let withDatUrlProduct = withRouter(ProductContainer);

export default connect(mapStateToProps, {
    getProductThunk,
    setCurrentPage,
    setPageSize,
    getCartsThunk
})(withDatUrlProduct);