import React from 'react';
import './DeliveryModule.css'
import { stockData } from '../../redux/data'; 
import PreloaderForUs from '../../common/PreloaderForUs';
import { useCookies } from 'react-cookie';



const Delivery = (props) => {
	const [ langu, setLangu ] = useCookies(['lang']);

	if (props.deliveries.length == 0) {
		return <PreloaderForUs title = {langu.lang != null ? stockData[langu.lang].delivery.title : stockData[props.lang].delivery.title}/>
	}

	let listDelivery = props.deliveries.map( u => <><div class="app-delivery-name"> {u.title}</div> <div class="app-delivery-term"> {u.description} </div> <hr /></>)

    return (
        <div class="container py-5">

		<div class="app-delivery">
			
			<div class="app-delivery-title text-center">
				<span> {langu.lang != null ? stockData[langu.lang].delivery.title : stockData[props.lang].delivery.title} </span>
			</div>

			<hr class="app-delivery-hr" />

			<div class="app-delivery-info">
				{ listDelivery }
			</div>


		</div>
		

	</div>
    )
}

export default Delivery;