import React from 'react';
import './SalonModule.css';
import { YMaps, Map, Placemark } from "react-yandex-maps";


const mapData = {
    center: [43.27340589050278, 76.96897884830834],
    zoom: 12,
  };
  
  const coordinates = [
    [43.338629, 76.98239],
    [43.271172, 76.94863],
  ];

const Salon = () => {
    return (
        <div class="app-salon container p-5">
            <div class="app-salon-cities">
                Almaty
            </div>
            <div class="app-salon-maps">
                <YMaps className = 'app-yandex-map'>
                    <Map width = '100%' height='600px' defaultState={mapData}>
                    {coordinates.map(coordinate => <Placemark geometry={coordinate} />)}
                    </Map>
                </YMaps>
            </div>
	    </div>
    );
}

export default Salon;