import React, { useEffect } from 'react';
import Preloader from '../../common/Preloader';
import './EventDetailModule.css';
import { NavLink } from 'react-router-dom';
import EventProduct from './SubEventProduct/EventProduct';


const EventDetail = (props) => {

    if (!props.eventDetail) {
        return <Preloader />
	}
	
    let from = (props.eventDetail.from_date).substring(0, 10).split('-');
	let to = (props.eventDetail.to_date).substring(0, 10).split('-');
	


	let product_list = props.eventProduct.map( u => <EventProduct id = {u.id} img_1 = {u.img_1}/>)
	
    
    return (
        <div class="container py-5">
		<div class="app-discount">

			<div class="app-discount-title text-center">
				
			</div>

			<hr class="app-discount-hr" />



			<div class="row">
				<div class="col-md-6 app-discount-image-div">
					<img class="app-discount-image" src={props.eventDetail.img} />
				</div>

				<div class="col-md-6 py-5">
					<div class="app-discount-name text-center"> {props.eventDetail.title}    </div>

					<div class="app-discount-data">
						<b>   : </b> {from[2]}.{from[1]}.{from[0]} - {to[2]}.{to[1]}.{to[0]}
					</div>

					<div class="app-discount-term">
						<b>:</b> {props.eventDetail.description}
					</div>
				</div>
			</div>


			<div class="row app-discount-base text-center mt-3">
				
					{  product_list }
			</div>
		
	    </div>
	</div>
    );
}

export default EventDetail;