import React from 'react';
import { useCookies } from 'react-cookie';
import {toast} from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

toast.configure();

const EventProduct = (props) => {

    const [ cookie, setCookie ] = useCookies(['product']);

    let products = [];
    const buyProduct = () => {
        toast.error("   ");
        let isTrue = true;
        if (cookie.product != null) {
            cookie.product.map((u, ide) => {
                if (u.id === props.id) {
                    products = cookie.product;
                    products[ide].count = parseInt(products[ide].count) + parseInt(1);
                    setCookie('product', products, {path: '/'});
                    isTrue = false;
                }
                
            });

            if (isTrue) {
                let state = {id: props.id, count: 1};
                products = cookie.product;
                products.push(state);
                products.sort( (a, b) => {
                    return a.id - b.id;
                });
                setCookie('product', products, {path: '/'});
            }

        }
        else {
            let state = {id: props.id, count: 1};
            products.push(state);
            products.sort( (a, b) => {
				return a.id - b.id;
            });
            setCookie('product', products, {path: '/'});
        }
    }


    return (
        <div class="col-xl-2 col-lg-3 col-md-4">
				<a href = {`/product/${props.id}`}>
				<img class="app-discount-sub-image" src={props.img_1} />
					</a>
				<div class="app-discount-middle" onClick = { buyProduct }>
					<div class="app-discount-text ">  </div>
				</div>
		</div>
    );

}

export default EventProduct;