import React from 'react';
import './PaginationModule.css';
import { brand } from '../Filter/Filter';
import { material } from '../Filter/Filter';
import { sex } from '../Filter/Filter';
import { radius } from '../Filter/Filter';
import { volumes } from '../Filter/Filter';
import { powers } from '../Filter/Filter';
import { stockData } from '../../../redux/data';
import { useCookies } from 'react-cookie';






const Pagination = (props) => {

    const [ langu, setLangu ] = useCookies(['lang']);

    let pagesCount = Math.ceil( props.count / 12 );
    let pages = [];

    
    let maxLeft = (props.currentPage - Math.floor(5 / 2));
    let maxRight = (props.currentPage + Math.floor(5 / 2));

    if (maxLeft < 1) {
        maxLeft = 1;
        maxRight = 5;
    }

    if (maxRight > pagesCount) {
        maxLeft = pagesCount - (5 - 1);

        if (maxLeft < 1) {
            maxLeft = 1;
        }
        maxRight = pagesCount
    }

    for (let i = maxLeft; i <= maxRight; i++) {
        pages.push(i);
    }

    
    
    return (
        <div class="app-product-pagination d-flex justify-content-center">


				<div onClick = { () => {
                    props.setCurrentPage(1)
                    props.getProductThunk(props.cat, props.subID, brand, material, sex, volumes, radius, powers, props.min, props.max, 1 );
                }} > {langu.lang != null ? stockData[langu.lang].pagination.prev:stockData[props.lang].pagination.prev } </div>

                {pages.map(i => {
                    return (
                        <div onClick = {() => {
                            props.setCurrentPage(i)
                            props.getProductThunk(props.cat, props.subID, brand, material, sex, volumes, radius, powers, props.min, props.max, i );
                        }}> {i} </div>
                    );
                })}
				
				



				<div onClick = { () => {
                    props.setCurrentPage(pagesCount);
                    props.getProductThunk(props.cat, props.subID, brand, material, sex, volumes, radius, powers, props.min, props.max, pagesCount);
                }}> {langu.lang != null ? stockData[langu.lang].pagination.next:stockData[props.lang].pagination.next } </div>


			</div>
    );
}

export default Pagination;