import React, { useMemo, useState } from 'react';
import { NavLink } from 'react-router-dom';
import Preloader from '../../common/Preloader';
import './DetailModule.css';
import { useCookies } from 'react-cookie';
import {toast} from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { stockData } from '../../redux/data';

toast.configure();


const Detail = (props) => {

    const [count, setCount] = useState(1);
    const [ cookie, setCookie ] = useCookies(['product']);
    const [ langu, setLangu ] = useCookies(['lang']);



    if (!props.detail) {
        return <Preloader />
    }

    console.log("Hee");
    //              FIND SUBCAT BY SUBID                    //
    const findSubcat = (subID, lists) => {

        return lists.map(u => subID == u.id ? u.value : '');
    }
    let subName;
    let catName;
    if (props.detail.cat == 'glasses') {
        subName = findSubcat(props.detail.subcat, props.glasses);
        catName = ''
    }
    else if (props.detail.cat == 'frames') {
        subName = findSubcat(props.detail.subcat, props.frames);
        catName = ''
    }
    else {
        subName = findSubcat(props.detail.subcat, props.outlets);
        catName = ''
    }



    //     INPUT CHANGE  //
    
    const handleChange = (e) => {
        setCount(e.target.value)  
    }

    // SAVE IN CART //
    let products = [];
    const addPorducts = () => {
        toast.error("   ");
        let isTrue = true;
        if (cookie.product != null) {
            cookie.product.map ((u, ide) => {
                if (u.id === props.detail.id) {
                    products = cookie.product;
                    products[ide].count =  parseInt(products[ide].count) + parseInt(count);
                    setCookie('product', products, {path: '/'});
					isTrue = false;	
                }

            });

            if (isTrue) {
                let state = {id: props.detail.id, count: count};
                products = cookie.product;
                products.push(state);
                products.sort( (a, b) => {
                    return a.id - b.id;
                });
                setCookie('product', products, {path: '/'});
            }

        }
        else {
            let state = {id: props.detail.id, count: count};
            products.push(state);
            products.sort( (a, b) => {
				return a.id - b.id;
            });
            setCookie('product', products, {path: '/'});

        }
    }


    return (
        <div class="container detail-block p-4">

            <div class='detail-way'> <span> {catName} </span> &raquo;  <span> {subName}  </span> &raquo; <span > {props.detail.title} </span></div>


            <div className="detail">
                <div className="detail-image">

                    <div id="carousel-thumb" class="carousel z-depth-1-half slide carousel-fade carousel-thumbnails" data-ride="carousel">
                        <div class="carousel-inner" role="listbox">
                            <div class="carousel-item active">
                                <img class="d-block w-100" src = {props.detail.img_1} alt="First slide" />
                            </div>
                            <div class="carousel-item">
                                <img class="d-block w-100" src={props.detail.img_2} alt="Second slide" />
                            </div>
                            <div class="carousel-item">
                                <img class="d-block w-100" src={props.detail.img_3} alt="Third slide" />
                            </div>
                        </div>

                        <a class="carousel-control-prev" href="#carousel-thumb" role="button" data-slide="prev">
                            <span class="carousel-control-prev-icon" aria-hidden="true"></span>
                            <span class="sr-only">Previous</span>
                        </a>
                        <a class="carousel-control-next" href="#carousel-thumb" role="button" data-slide="next">
                            <span class="carousel-control-next-icon" aria-hidden="true"></span>
                            <span class="sr-only">Next</span>
                        </a>

                        <br />
                        <br />

                        <ol class="carousel-indicators">
                            <li data-target="#carousel-thumb" data-slide-to="0" class="active mr-2 app-detail-list-image"> <img class="d-block w-100 im img-fluid" src={props.detail.img_1} /></li>
                            <li data-target="#carousel-thumb" class="ml-5 app-detail-list-image" data-slide-to="1"><img class="d-block w-100 im img-fluid" src={props.detail.img_2} /></li>
                            <li data-target="#carousel-thumb" class="ml-5 app-detail-list-image" data-slide-to="2"><img class="d-block w-100 im img-fluid" src={props.detail.img_3} /></li>
                        </ol>

                    </div>


                </div>
                

                            <div class="detail-info p-5">
                                <h1 class="detail-title"> {props.detail.title} </h1>

                                <p>{langu.lang != null ? stockData[langu.lang].detail.title: stockData[props.lang].detail.title}</p>

                                <div class="d-flex">
                                    {props.detail.is_discount ? <h2 class="detail-price mr-4"> {props.detail.price * (1 - (props.detail.discount / 100))} KZT </h2>
                                        : <h2 class="detail-price mr-4"> {props.detail.price} KZT </h2>}
                                    {props.detail.is_discount && <del class="not-detail-price"> {props.detail.price} </del>}
                                </div>

                                <input class="form-control form-control-sm w-25" type="number" min="1" value={count} onChange={handleChange} />
                                <br />
                                <button class="btn btn-lg" onClick = {  addPorducts }>
                                    {langu.lang != null ? stockData[langu.lang].detail.button: stockData[props.lang].detail.button}
                                </button>

                                <hr />

                                <h1 class="detail-char-header">{langu.lang != null ? stockData[langu.lang].detail.har: stockData[props.lang].detail.har}</h1>

                                <table class="detail-char-table  table-sm table-borderless">
                                    <tr>
                                        <th>
                                            {langu.lang != null ? stockData[langu.lang].detail.cat: stockData[props.lang].detail.cat}
                            </th>
                                        <td>
                                        {subName}
                            </td>
                                    </tr>

                                    {props.detail.brand && <tr>
                                        <th>
                                            {langu.lang != null ? stockData[langu.lang].detail.brand: stockData[props.lang].detail.brand}
                            </th>
                                        <td>
                                            {props.detail.brand}
                                        </td>
                                    </tr>}

                                    {props.detail.material && <tr>
                                        <th>
                                        {langu.lang != null ? stockData[langu.lang].detail.material: stockData[props.lang].detail.material}
                            </th>
                                        <td>
                                            {props.detail.material}
                                        </td>
                                    </tr>}

                                    {props.detail.sex && <tr>
                                        <th>
                                            {langu.lang != null ? stockData[langu.lang].detail.sex: stockData[props.lang].detail.sex}
                            </th>
                                        <td>
                                            {props.detail.sex}
                                        </td>
                                    </tr>}

                                    {props.detail.opt_power && <tr>
                                        <th>
                                        {langu.lang != null ? stockData[langu.lang].detail.power: stockData[props.lang].detail.power}
                            </th>
                                        <td>
                                            {props.detail.opt_power}
                                        </td>
                                    </tr>}

                                    {props.detail.volume && <tr>
                                        <th>
                                        {langu.lang != null ? stockData[langu.lang].detail.volume: stockData[props.lang].detail.volume}
                            </th>
                                        <td>
                                            {props.detail.volume}
                                        </td>
                                    </tr>}

                                    {props.detail.radius && <tr>
                                        <th>
                                        {langu.lang != null ? stockData[langu.lang].detail.radius: stockData[props.lang].detail.radius}
                            </th>
                                        <td>
                                            {props.detail.radius}
                                        </td>
                                    </tr>}
                                </table>
                            </div>

                            <div class="detail-description p-5">
                                <h1 class="text-center p-3"> {langu.lang != null ? stockData[langu.lang].detail.opisania: stockData[props.lang].detail.opisania}</h1>
                                <p> {props.detail.description} </p>
                            </div>
            </div>

                        
                    </div>
    );
};

const areEqual = (prevProps, nextProps) => {
    return prevProps.detail === nextProps.detail
        && prevProps.glasses === nextProps.glasses
        && prevProps.frames === nextProps.frames
        && prevProps.outlets === nextProps.outlets;
}
export default React.memo(Detail, areEqual);